@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.3.0/css/all.min.css);

header{
  width: 100%;
  border:none;
}
.h_wrap{
  width:80%;
  margin:0 auto;
}
.h_top{
  width:100%;
  margin-top: 50px;
  display: flex;
  justify-content:space-between;
}
.h_top_left a{
  margin-left: 20px;
}
i.fa-brands{
  transform: translateY(10%);
  font-size: 150%;
  margin-right: 5px;
}

i.fa-solid{
  font-size:40px;
}
.h_wrap h1{
  margin: 0 auto;
  width:200px;
  border-bottom: solid 1px #333;
}

.h_wrap p{
  text-align: center;

}
.h_bottom nav ul {
  margin: 60px 0px;
  display: flex;
  justify-content: center;
  width: 100%;

}
.h_bottom nav ul li{
  font-weight: 600;
}
.h_bottom nav ul li a{
  padding:5px 15px;
}