.banner{
  width:100%;
}
.banner ul{
  display: flex;
  justify-content: space-between;
}
.banner ul li{
  width:33.3%;
  gap: 1%;

}
.banner ul li img{width:100%;}


.slide h2{display: none;}
.slide_wrap{
  width: 100%;
  overflow: hidden;
}
.slide_wrap ul {
  width:200%;
  display: flex;
}
.slide_wrap ul li{
  width:100%;
  position: relative;
}
.slide_wrap ul li img{
  width: 100%;
}
.contents{
  display: flex;
  width:100%;
  overflow: hidden;
}
.contents li{
  width: 50%;
  transition: .4s;
  overflow: hidden;
}

.contents li img{
  border: solid #333 1px;
  width:100%;

}
.link{
  padding: 2% 8%;
  border: 3px solid #fff;
  font-weight: 600;
  font-size: 120%;
  position: relative;
  left: 10%;
  bottom: 30%;
  color: #fff;
  transition:.4s;
}
.contents li p{
  margin-left: 10%;
  transform: translateY(150%);
  color: #fff;
  font-size: 260%;
  font-weight: bold;
}

.contents li:hover{
  filter: grayscale(.7);
}
.contents li a:hover{
  border-color: #555;
  background-color: #555;
}

.new h2{text-align: center;}
.new_con{
  display: grid;
  grid-template-columns:repeat(3, auto) ;
  grid-template-rows: (3,280px);
  grid-gap: 30px;
  padding: 50px;
  box-sizing: border-box;
}

.new_con li img{
  border: solid 1px #555;
  width: 100%;
}

.cartA{
  text-decoration: none;
  margin-top: 10%;
  color:#000;
}
.event, .event img, .p_review_wrap ul li img{
  width: 100%;
}
.p_review_wrap{
  width:80%;
  overflow: hidden;
  margin: 0 auto;
}
.p_review_wrap ul{
  display: flex;
  width:298%;
  position: relative;

}
.p_review_wrap ul li{
  width: 100%;
  position: relative;
  bottom: 0px;left: 0px;
}
.p_review_wrap ul li img{
  width:100%;  
}

