footer{
  background-color: #d9d9d9;
  width: 100%;
}
.f_wrap{
  width: 80%;
  margin: 0 auto;
  line-height: 40px;
}
.f_wrap dl{
  border-bottom: solid 1px #333;
}
.f_wrap h1{
  width:20%;
  margin: 0 auto;
  padding-top: 5%;
}
.f_wrap h1 img{width: 100%;}
.f_icons img{
  width:30px;
  margin: 15px 10px;
}
.f_wrap > address{
  font-style: normal;
  text-align: center;
  padding: 2% 0%;
}
.f_nav ul{
  display: flex;
  justify-content:flex-start;
}
.f_nav ul li{
  margin-left: 5%;
}
